$primary1: #7757FF;
$primary2: #323254;
$primary3: #D37A05;
$primary4: #FFFCED;
$secondary1: #EC9FFF;
$secondary2: #DBD8FF;
$secondary3: #F4F0DD;
$wrong: #FF0000;
$disable: #BDBDBD;
$black: #000000;
$white: #ffffff;
$shadow: #404040;
$larkenFont: 'Larken-Regular', sans-serif;
$sharpSansSemiboldFont: 'SharpSansNo1-Semibold', sans-serif;
$sharpSansBookFont: 'SharpSansNo1-Book', sans-serif;

#main-wrapper {
    width: 100vw;
    min-width: 100%;
}

#wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

body {
    background: $primary4;
    height: 100vh;
    overflow-x: hidden;
    max-width: 100vw;
    width: 100vw;
    position: relative;
    min-height: 100vh;

    &.error404 {
        overflow: hidden;
    }

    &.popup-opened {
        overflow: hidden;

        @media screen and (max-width: 768px) {
            height: 100vh;
        }
    }
}

.container {
    padding: 0 16px;
    margin: 0 auto;

    @include min-break('1360px') {
        max-width: 1240px;
        padding: 0;
    }

    &.full-width {
        padding: 0;
        width: 100vw;
        max-width: 100vw;
    }
}

.container-small {
    padding: 0 25px;
    margin: 0 auto;

    @include min-break('1360px') {
        max-width: 1030px;
        padding: 0;
    }
}

.site-header__menu,
.site-footer__menu {
    .menu-item {
        &:last-of-type {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            background: $primary1;
            box-shadow: -1vw 1vw 0 $shadow;
            cursor: pointer;
            width: 108px;
            height: 38px;
            margin: 7px 0 0;

            @include min-break('1024px') {
                width: 119px;
                height: 34px;
                margin: 0;
                box-shadow: -0.41vw 0.41vw 0 $shadow;
            }

            &:after {
                content: "";
                position: absolute;
                top: 0.3vw;
                left: -0.7vw;
                width: 1.3vw;
                height: 1.4vw;
                background-color: $shadow;
                transform: rotate(45deg);
                z-index: -1;
                transition: top 0s ease-in-out;

                @include min-break('1024px') {
                    top: 0.28vw;
                    left: -0.35vw;
                    width: 1vw;
                    height: 0.6vw;
                }
            }

            &:before {
                content: '';
                position: absolute;
                bottom: -0.7vw;
                right: 0.25vw;
                width: 1.4vw;
                height: 1.3vw;
                background-color: $shadow;
                transform: rotate(45deg);
                z-index: -1;
                transition: top 0s ease-in-out;

                @include min-break('1024px') {
                    bottom: -0.1vw;
                    right: 0.05vw;
                    width: 1vw;
                    height: 0.6vw;
                }
            }

            &:active {
                top: 6px;
                left: -6px;
                box-shadow: none;
                text-decoration: none;

                &:after,
                &:before {
                    top: 10px;
                    left: 10px;
                }
            }

            @include min-break('1024px') {
                &:hover {
                    background: #D37A05;
                }
            }

            a {
                color: $primary4;
                margin: 0;
            }
        }

        @include min-break('1024px') {
            &:not(:last-of-type):hover {
                a, span {
                    color: $primary1;
                }
            }
        }

        a, span {
            @extend %paragraph;
            margin: 0;
        }
    }
}

.cutter {
    clip-path: polygon(0.07% 44.00%, 36.66% 43.67%, 100% 0%, 100% 100%, 0% 100%);
    background: #FFFCED;
    position: relative;
    top: 1px;
}
