.breeze-vacancies {
    padding: 35px 0 120px;
    position: relative;
    background: #323254;
    z-index: 12;

    .container {
        text-align: center;
    }

    @include min-break('1024px') {
        z-index: 10;
        padding: 104px 0;
    }

    &:before {
        content: '';
        position: absolute;
        top: -38px;
        left: 0;
        background: #323254;
        width: 100%;
        height: 40px;
        clip-path: polygon(64% 8%, 100% 0, 100% 100%, 0 100%, 0% 98%);
    }

    &:after {
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        background: #252544;
        width: 100%;
        height: 600px;
        clip-path: polygon(80% 97%, 100% 58%, 100% 100%, 0 100%, 0 40%);
        display: none;

        @include min-break('1024px') {
            display: block;
        }
    }

    &__title {
        text-align: center;
        margin: 0 0 51px;

        @include min-break('1024px') {
            margin: 0 0 39px;
        }

        h3 {
            font-size: 21px;
            line-height: 28px;
            color: $primary4;
            font-family: $sharpSansSemiboldFont;

            @include min-break('1024px') {
                font-size: 45px;
                line-height: 42px;
            }
        }
    }
}
.vacancies-container {
    $self : &;
    text-align: left;

    @include min-break('980px') {
        display: flex;
        flex-direction: row-reverse;
        gap: 0 20px;
    }
    &__items-container {
        display: flex;
        flex-direction: column;
        gap: 17px 0;
        position: relative;
        z-index: 2;

        @include min-break('768px') {
            gap: 17px 18px;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
        @include min-break('980px') {
            width: 50%;
        }
    }
    &__banner-container {
        position: relative;
        margin-bottom: 26px;

        @include min-break('980px') {
            width: 50%;
            margin-bottom: 0;
        }
        &:after {
            content: "";
            display: block;
            position: absolute;
            z-index: 1;
            background: $primary1;
            width: calc(100% - 10px);
            height: calc(100% - 10px);
            bottom: -10px;
            right: 0;

            @include min-break('980px') {
                bottom: 0;
            }
        }
    }
    &__banner-wrapper {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        margin: 0 10px 10px 0;
        position: relative;
        height: calc(100% - 10px);
        z-index: 5;

        @include min-break('980px') {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
        }
    }
    &__banner {
        padding-top: 56.25%;

        &-content {
            background: $secondary2;
            clip-path: polygon(0 0,75% 25%,75% 100%,0 100%);
            padding: 42px 58px 16px 20px;
            position: relative;

            @include min-break('980px') {
                clip-path: polygon(0 5%,76% 39%,76% 100%,0 100%);
                padding: 82px 58px 16px 38px;
            }

            &:before {
                content: '';
                background: url('./../images/quote-el-1.svg') 0 0 no-repeat;
                width: 75px;
                height: 46px;
                position: absolute;
                left: 8px;
                bottom: 90px;
                z-index: 1;

                @include min-break('980px') {
                    bottom: 100px;
                    height: 89px;
                    left: 18px;
                    width: 135px;
                    background-size: contain;
                }
            }
            &:after {
                content: '';
                background: url('./../images/quote-el-2.svg') 0 0 no-repeat;
                width: 43px;
                height: 40px;
                position: absolute;
                left: 190px;
                bottom: 10px;
                z-index: 1;

                @include min-break('980px') {
                    bottom: 24px;
                    height: 74px;
                    left: 58%;
                    background-size: cover;
                    width: 77px;
                }
            }
        }
        &-position,
        &-author,
        &-quote {
            font-size: 12px;
            line-height: 15px;
            font-family: $sharpSansBookFont;
            margin-bottom: 17px;
            color: $primary2;
        }
        &-quote {
            max-width: 180px;
            position: relative;
            z-index: 2;

            @include min-break('980px') {
                font-size: 20px;
                line-height: 25px;
                max-width: none;
                margin-bottom: 29px;
            }
        }
        &-author {
            line-height: 16px;
            margin-bottom: 5px;
            font-family: $sharpSansSemiboldFont;

            @include min-break('980px') {
                font-size: 16px;
                line-height: 14px;
            }
        }
        &-position {
            margin-bottom: 0;

            @include min-break('980px') {
                font-size: 16px;
                line-height: 16px;
            }
        }
    }
    &__item {
        background: #474774;
        transition: all 0.5s;

        @include min-break('768px') {
            display: flex;
        }

        &:hover {
            background: $primary4;
            cursor: pointer;

            .vacancies-container__item-link-container,
            .vacancies-container__item-department,
            .vacancies-container__item-name,
            .vacancies-container__item-location {
                color: $primary2;
            }
            .vacancies-container__item-department span {
                border-left: 1px solid $primary2;
            }
            .vacancies-container__item-link-container {
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='10' viewBox='0 0 16 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.1353 9.42725L15.5625 5L11.1353 0.572754' stroke='%23323254' stroke-linejoin='bevel'/%3E%3Cpath d='M15.4609 5L0.439232 5' stroke='%23323254' stroke-linejoin='bevel'/%3E%3C/svg%3E%0A");
            }
        }

        &-link {
            display: block;
            padding: 24px 25px;

            @include min-break('768px') {
                display: flex;
                flex-direction: column;
                width: 100%;
                padding: 19px 28px 14px;
            }
        }
        &-link-container,
        &-department,
        &-name,
        &-location {
            color: $primary4;
            font-family: $sharpSansBookFont;
            display: block;
        }
        &-location {
            font-size: 13px;
            line-height: 18px;
            margin-bottom: 3px;

            @include min-break('768px') {
                line-height: 23px;
            }
        }
        &-name {
            font-size: 20px;
            line-height: 27px;
            margin-bottom: 16px;
            font-family: $sharpSansSemiboldFont;

            @include min-break('768px') {
                flex: 1;
                line-height: 22px;
                margin-bottom: 21px;
            }
        }
        &-department {
            font-size: 18px;
            line-height: 24px;
            margin-bottom: 26px;

            span {
                border-left: 1px solid $primary4;
                padding-left: 12px;
                margin-left: 12px;
            }
        }
        &-link-container {
            font-size: 13px;
            line-height: 32px;
            display: inline;
            text-transform: capitalize;
            background-image: url("data:image/svg+xml,%3Csvg width='16' height='10' viewBox='0 0 16 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.1353 9.42725L15.5625 5L11.1353 0.572754' stroke='%23FFFCED' stroke-linejoin='bevel'/%3E%3Cpath d='M15.4609 5L0.439232 5' stroke='%23FFFCED' stroke-linejoin='bevel'/%3E%3C/svg%3E");
            background-repeat: no-repeat;
            background-position: 100% 50%;
            padding-right: 23px;

            @include min-break('768px') {
                background-position: 84px 50%;
            }
        }
    }
    &.short-view {
        #{ $self }__items-container {

        }
    }
    &__full-container {
        margin-top: 17px;
        gap: 17px 0;
        display: grid;
        text-align: left;
        position: relative;
        z-index: 10;

        @include min-break('768px') {
            gap: 17px 18px;
            grid-template-columns: repeat(2, 1fr);
        }
        @include min-break('1024px') {
            gap: 17px 18px;
            grid-template-columns: repeat(4, 1fr);
        }
    }
}

.load-more-jobs {
    background: $primary1;
    font-size: 14px;
    line-height: 18px;
    color: $primary4;
    font-family: $sharpSansSemiboldFont;
    padding: 10px 16px;
    transition: all 0.5s;
    position: relative;
    z-index: 5;
    border: none;
    margin: 17px auto 0;

    @include min-break('1024px') {
        margin: 39px auto 0;
        padding: 5px 21px;
        font-size: 16px;
        line-height: 32px;
    }

    &:hover {
        background: $primary3;
        cursor: pointer;
    }
}
