.content-block {

    .single-resources &,
    .single-events & {

        &:last-of-type {
            padding: 0 0 65px;

            @include min-break('1024px') {
                padding: 0 0 134px;
            }
        }

        .bodyM {
            color: $black;
        }

        li {
            color: $black;
        }

        img {
            margin: 10px 0 40px;

            @include min-break('1024px') {
                margin: 50px 0 80px;
            }
        }

        a {
            padding: 5px 21px;
            display: flex;
            justify-content: center;
            margin: 56px auto 0;

            @include min-break('1024px') {
                margin: 90px auto 0;
            }
        }

        h3 {
            margin: 0 auto;
            max-width: 260px;

            @include min-break('480px') {
                max-width: 100%;
            }
        }

        h4 {
            margin: 40px 0 16px;

            @include min-break('1024px') {
                margin: 70px 0 36px;
            }
        }
    }

    .privacy-policy &,
    .terms-of-service & {
        transition: all 0s ease-in;
        -webkit-transition: all 0s ease-in;
        overflow: hidden;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 10;
        margin: 350px 0 0;

        @include min-break('1024px') {
            margin: 500px 0 0;
        }

        &:last-of-type {
            .content-block__content_wrap {
                padding: 0 0 64px;

                @include min-break('1024px') {
                    padding: 0 0 104px;
                }
            }
        }

        .cutter {
            height: 105px;
        }

        &__content_wrap {
            background: $primary4;
            height: max-content;
            position: relative;
        }
    }

    &__content_wrap {
        h3 {
            font-size: 40px;
            text-align: center;

            @include min-break('1024px') {
                font-size: 70px;
            }
        }

        div > a {
            margin: 0 auto;
        }

        p a {
            font-family: inherit;
            font-size: inherit;
            color: inherit;
        }

        .bodySmall {
            margin: 0 0 35px;
            font-size: 16px;

            &.no-space {
                margin: 0;
            }

            &:last-of-type {
                margin: 0;
            }
        }

        .subtitleMBold {
            margin: 0 0 11px;
            font-size: 16px;

            @include min-break('1024px') {
                font-size: 25px;
            }
        }

        li {
            @extend %paragraph;
            font-family: $sharpSansBookFont;
            list-style-type: none;
            padding-left: 30px;
            position: relative;
            margin: 0 0 30px;

            &:after {
                position: absolute;
                content: '';
                display: block;
                left: 0;
                top: 10px;
                width: 8px;
                height: 8px;
                border-radius: 50%;
                background: $black;
                z-index: 1;
            }

            @include min-break('1024px') {
                font-weight: 800;
                font-size: 18px;
                line-height: 157%;
            }
        }
    }
}
