%heading {
    font-family: $larkenFont;
    font-style: normal;
    font-weight: 400;
    line-height: 104.02%;
    color: $primary2;
    margin: 0 0 20px;

    @include min-break('1024px') {
        line-height: 94.02%;
        margin: 0 0 36px;
    }
}

%paragraph {
    font-family: $sharpSansSemiboldFont;
    font-style: normal;
    font-weight: 800;
    line-height: 1.55;
    color: $primary2;
    margin: 0 0 16px;

    @include min-break('1024px') {
        font-size: 16px;
        margin: 0 0 30px;
        line-height: 1.97;
    }
}

%btn-text {
    @extend %paragraph;
    text-decoration: none;
    position: relative;
    font-size: 13px;

    &:after {
        position: absolute;
        content: '';
        display: block;
        right: -30px;
        top: 50%;
        transform: translateY(-50%);
        width: 15px;
        height: 8px;
        z-index: 1;
        background: url(data:image/svg+xml;base64,) center center no-repeat;
        background-size: 15px;

        @include min-break('768px') {
            width: 21px;
            height: 14px;
            background-size: 21px;
        }
    }

    @include min-break('1025px') {
        &:hover {
            color: #7757FF;

            &:after {
                background: url(data:image/svg+xml;base64,) center center no-repeat;
            }
        }
    }
    @include min-break('768px') {
        font-size: 16px;
    }
}

