.button {
    line-height: 197% !important;
    width: max-content;
    cursor: pointer;
    background: $primary1;
    color: $primary4;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: -0px 0px 0 $shadow !important;
    position: relative;
    transition: all .2s linear;
    top: 0;
    left: 0;
    z-index: 1;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -khtml-user-select: none; /* Konqueror */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none;

    a {
        -webkit-touch-callout: none; /* iOS Safari */
        -webkit-user-select: none; /* Chrome/Safari/Opera */
        -khtml-user-select: none; /* Konqueror */
        -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
        user-select: none;
        all: unset;

        @media screen and (orientation: portrait) {
            &.button-primary:active {
                background: $primary3 !important;
                color: $primary4 !important;
            }
            &.button-secondary:active {
                background: $primary1 !important;
                color: $primary4 !important;
            }
        }
    }

    &-primary {
        background: $primary1;
        color: $primary4 !important;
    }

    &-secondary {
        background: $primary4 !important;
        color: $primary1;
    }
}

@media screen and (orientation: portrait) {
    .button-primary:active {
        background: $primary3 !important;
        color: $primary4 !important;
    }
    .button-secondary:active {
        background: $primary1 !important;
        color: $primary4 !important;
    }
}


@media screen and (orientation: landscape) {
    .button:hover {
        left: 8px;
        top: -8px;
    }

    .button::before, .button::after {
        position: absolute;
        content: '';
        transition: all .2s ease-in-out;
    }
    .button::before {
        bottom: 0;
        height: 0;
        width: 100%;
        right: 4px;
        transform: skewX(0deg);
        background-color: $shadow;
        opacity: 0;
    }
    .button::after {
        left: 0px;
        height: 100%;
        width: 0;
        bottom: -4px;
        transform: skewY(0deg);
        background: $shadow;
        opacity: 0;

    }
    .button:hover::before {
        bottom: -8px;
        height: 8px;
        width: 100%;
        right: 4px;
        transform: skewX(-45deg);
        background-color: $shadow;
        opacity: 1;
    }
    .button:hover::after {
        left: -7.5px;
        height: 100%;
        width: 8px;
        bottom: -4px;
        transform: skewY(-45deg);
        background: $shadow;
        opacity: 1;
    }

}
