.site-header {
    position: sticky;
    top: 0;
    left: 0;
    background-color: $primary4;
    width: 100%;
    z-index: 9999;

    .popup-opened & {
        display: none;
    }

    &__header-wrapper {
        display: flex;
        align-items: center;
        height: 106px;

        @include min-break('1024px') {
            height: 90px;
        }
    }

    &__logo {
        width: 92.43px;

        @include min-break('1024px') {
            width: 130px;
        }
    }

    &__menu-wrapper {
        flex-grow: 1;
        display: flex;
        justify-content: flex-end;

        @include min-break('1024px') {
            display: flex !important;
        }

        @include max-break('1023px') {
            display: none;
            position: fixed;
            width: 100vw;
            height: 100vh;
            top: 0;
            left: 0;
            box-sizing: border-box;
            background: $primary2;

            &.open {
                display: flex;
            }
        }
    }

    &__menu {
        display: flex;
        align-items: center;

        @include max-break('1023px') {
            z-index: 3;
            position: absolute;
            width: 100%;
            height: calc(100vh + 20px);
            box-sizing: border-box;
            top: 0;
            right: 0;
            gap: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            background: $primary4;
        }

        @include max-break('414px') {
            padding-left: 28.26vw;
            align-items: flex-start;
        }
    }

    .menu-item {

        &:last-of-type {
            a {
                font-size: 14px;

                @include min-break('1024px') {
                    font-size: 16px;
                    line-height: 197%;
                }
            }
        }

        @include min-break('1024px') {
            margin-right: 25px;

            &:last-of-type {
                margin-right: 0;
                margin-left: 12px;
            }
        }

        a, span {
            font-size: 20px;

            @include min-break('1024px') {
                font-size: 16px;
            }
        }
    }

    &__header-burger {
        cursor: pointer;
        flex-grow: 1;
        display: flex;
        justify-content: flex-end;

        @include min-break('1024px') {
            display: none;
        }
    }

    &__header-closebtn {
        display: none;
        position: fixed;
        top: 9.86vw;
        right: 4.8vw;
        z-index: 11;
        cursor: pointer;

        @include min-break('1024px') {
            display: none !important;
        }

        &.open {
            display: flex;
        }
    }
}
