.job-subscribe {
    background: #252544;
    position: relative;
    z-index: 12;
    padding: 50px 0 91px;

    &:before {
        content: '';
        position: absolute;
        top: -80px;
        left: 0;
        background: #252544;
        width: 100%;
        height: 82px;
        clip-path: polygon(70% 97%, 100% 38%, 100% 100%, 0 100%, 0% 38%);

        @include min-break('1024px') {
            display: none;
        }
    }

    &__title {
        text-align: center;
        margin: 0 auto 18px;
        max-width: 750px;

        h3 {
            color: $primary4;
            font-size: 40px;
            line-height: 42px;

            @include min-break('1024px') {
                font-size: 74px;
                line-height: 70px;
                margin: 0 auto 35px;
            }
        }
    }

    &__description {
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        color: $white;
        max-width: 236px;
        margin: 0 auto 42px;
        font-family: $sharpSansBookFont;

        @include min-break('768px') {
            max-width: none;
        }
        @include min-break('1024px') {
            font-size: 25px;
            line-height: 30px;
            margin: 0 auto 69px;
        }
    }
    &__form-area {
        margin: 0 24px;

        form {
            display: flex;
            max-width: 522px;
            margin: 0 auto;
        }
        input {
            background: $white;
            font-family: $sharpSansBookFont;
            font-size: 16px;
            border: 1px solid $white;
            width: calc(100% - 77px);
            padding: 10px 16px;
            transition: all 0.5s;

            &:focus-visible,
            &:focus {
                border: 1px solid $primary1;
            }

            &::placeholder {
                font-size: 16px;
                line-height: 23px;
                opacity: 1;
                color: $primary1;
                font-family: $sharpSansSemiboldFont;
            }
        }

    }
    &__submit {
        font-size: 14px;
        line-height: 18px;
        text-align: center;
        color: $primary4;
        font-family: $sharpSansSemiboldFont;
        background: $primary1;
        border: none;
        padding: 10px 16px;
        width: 77px;
    }
}
