.come-workwith-us {
    .site-footer {
        position: relative;
        z-index: 10;
    }
}
.site-footer {
    height: 259px;
    position: relative;

    &:after {
        position: absolute;
        content: '';
        display: block;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
        background-color: $primary2;
    }

    @include max-break('768px') {
        height: 615px;
    }

    @include max-break('1023px') {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__wrapper {
        padding: 40px 0 26px;

        @include max-break('1023px') {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }

    &__logo {
        width: 144.92px;

        @include max-break('1023px') {
            width: 130px;
            margin-bottom: 20px;
        }

        &-nav {
            display: flex;
            align-items: center;
        }
    }

    &__menu-wrapper {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include max-break('1023px') {
            flex-direction: column;
        }
    }

    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include max-break('1023px') {
            flex-direction: column;
            gap: 17px;
        }

        .menu-item {

            @include min-break('1024px') {
                margin-right: 25px;

                &:last-of-type {
                    margin-right: 0;
                    margin-left: 12px;
                }
            }

            @include min-break('1440px') {
                margin-right: 67px;

                &:last-of-type {
                    margin-left: 0;
                }
            }

            &:not(:last-of-type) {
                a, span {
                    color: $secondary3;

                    @include min-break('1024px') {
                        &:hover {
                            color: $primary1;
                        }
                    }
                }
            }

            @include max-break('1023px') {
                &:last-of-type {
                    margin-top: 22px;
                }
            }
        }
    }

    &__socials {
        padding: 38px 0 51px;
        display: flex;
        gap: 11px;

        @include max-break('1023px') {
            justify-content: center;
            padding: 30px 0 0;
        }
    }

    &__social {
        @include min-break('1024px') {
            &:hover {
                .background {
                    fill: $secondary1;
                }
            }
        }

        .twitter {
            width: 38.62px;
        }

        .instagram {
            width: 40.93px;
        }

        .facebook {
            width: 41.71px;
        }

        .mail {
            width: 42.48px;
        }

        .background {
            fill: #474774;
            cursor: pointer;
        }
    }

    &__all-rights {
        color: $secondary3;
        font-family: $sharpSansBookFont;
        font-size: 16px;
        margin: 0;

        @include min-break('1024px') {
            font-size: 14px;
            color: $secondary2;
        }
    }

    &__menu_bottom {
        align-items: center;
        display: flex;
        margin: 30px 0;

        @include min-break('1024px') {
            flex-direction: row;
            margin: 0;
        }

        .menu-item {
            margin-right: 25px;

            &:last-of-type {
                margin-right: 0;
            }

            a, span {
                position: relative;
                font-family: $sharpSansBookFont;
                font-weight: 800;
                font-size: 14px;
                line-height: 138%;
                color: $secondary2;

                @include min-break('1024px') {
                    &:hover {
                        color: $primary1;
                    }
                }

                &:after {
                    position: absolute;
                    content: '';
                    display: block;
                    left: 0;
                    bottom: -2px;
                    width: 100%;
                    height: 1px;
                    z-index: 1;
                    background: #53538C;
                }
            }
        }

        &-wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: column-reverse;

            @include min-break('1024px') {
                flex-direction: row;
            }
        }
    }
}
