.be-yourself {
    background-color: $primary1;
    position: relative;
    padding-bottom: 108px;
    z-index: 2;

    &__top-cutter {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 100%;
        margin-bottom: -1px;
        height: calc(100vw * .2);
        background-image: url("data:image/svg+xml,%3Csvg width='1262' height='233' viewBox='0 0 1262 233' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1261.5 232.5H0L1059 0H1261.5V232.5Z' fill='%237757FF'/%3E%3C/svg%3E%0A");
        background-position: right bottom;
        background-repeat: no-repeat;
        background-size: contain;
        z-index: 3;
    }
    &__clouds {
        overflow: hidden;
        position: absolute;
        top: -100px;
        left: 0;
        right: 0;
        bottom: 0;
    }
    &__cloud {
        position: absolute;
        z-index: 10;
        top: 100px;
        right: 5%;
        width: 69px;

        @include min-break('768px') {
            width: 266px;
        }

        &:first-child {
            top: 70px;
            right: 30%;
            width: 50px;

            @include min-break('768px') {
                width: 132px;
                top: 20px;
            }
        }
        &:last-of-type {
            top: 450px;
            left: -40px;
            width: 86px;

            @include min-break('768px') {
                width: 346px;
                top: 346px;
                left: -40px;
            }
        }
    }
    &__content-wrapper {
        max-width: 811px;
        text-align: center;
        margin: auto;
        position: relative;
        z-index: 10;
    }
    &__content-title {
        font-size: 30px;
        line-height: 31px;
        margin-bottom: 16px;
        color: $primary4;
        font-family: $larkenFont;
        padding: 48px 0 0;

        @include min-break('768px') {
            font-size: 50px;
            line-height: 51px;
            padding: 0;
        }

        @include min-break('1024px') {
            font-size: 74px;
            line-height: 70px;
            margin-bottom: 35px;
        }
    }
    &__content {
        font-size: 16px;
        line-height: 23px;
        color: $primary4;
        margin-top: 16px;
        font-family: $sharpSansBookFont;

        @include min-break('768px') {
            font-size: 20px;
            line-height: 27px;
        }

        @include min-break('1024px') {
            font-size: 25px;
            line-height: 30px;
        }
    }
}
