.most-popular {
    margin: 20px 0 65px;

    @include min-break('1200px') {
        margin: 20px 0 134px;
    }

    h5 {
        text-align: center;
        margin: 0 0 45px;
        font-family: $sharpSansSemiboldFont;

        @include max-break('914px') {
            font-size: 30px;
        }
    }

    .container-small {
        @include max-break('414px') {
            padding: 0 0 0 16px;
        }
    }

    &__items {
        max-width: 100%;
        overflow: hidden;
    }

    &__item {
        background: $white;
        display: flex;
        flex-direction: column;
        border: 0.735033px solid $secondary3;
        min-width: 300px;

        &:not(:first-of-type) {

            .most-popular__learn-more {
                font-size: 13px;

                &:after {
                    background-size: 16px;
                    height: 9px;
                    width: 16px;
                    right: -24px;
                }
            }
        }
    }

    &__download_wrap {
        margin-top: auto;
    }

    &__locked {
        width: 34px;
        height: 34px;
        position: absolute;
        top: 14px;
        right: 14px;
        background: $primary2;
        border: 1px solid $secondary3;

        &:after {
            position: absolute;
            content: '';
            display: block;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 16px;
            height: 21px;
            z-index: 1;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
        }
    }

    &__title {
        margin: 0 0 24px;
        text-overflow: ellipsis;
        overflow: hidden;
        max-width: 290px;

        @include min-break('1024px') {
            max-width: 250px;
        }

        @include min-break('1200px') {
            max-width: 290px;
        }
    }

    &__excerpt {
        display: none;
    }

    &__image {
        max-height: 147px;
        min-height: 147px;
        width: -webkit-fill-available;
        height: 100%;
        display: block;
        object-fit: cover;

        @include min-break('1024px') {
            max-height: 178px;
            min-height: 178px;
        }

        &-wrap {
            position: relative;
            padding: 12px 12px 0;
        }
    }

    &__tags {
        padding: 2px 20px;
        display: inline-block;
        position: absolute;
        bottom: 0;
        left: 0;
        background: $secondary1;
        border: 1px solid $secondary3;

        &.brown {
            background: $primary3;
        }

        &.light-purple {
            color: $primary1;
            background: $secondary2;
        }

        &.purple {
            background: $primary1;
        }

        &.dark-blue {
            background: $primary2;
        }

        &.dark-cream {
            color: $primary2;
            background: #D8D1B6;
        }

        a {
            font-family: $sharpSansSemiboldFont;
            font-weight: 800;
            font-size: 14px;
            text-decoration: none;
            line-height: 1.97;
            text-align: center;
            text-transform: capitalize;
            color: $primary4;

            @include min-break('768px') {
                font-size: 16px;
            }
        }
    }

    &__learn-more {
        @extend %btn-text;
        font-family: $sharpSansSemiboldFont;
        text-transform: capitalize;
        margin: 0;

        &-wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    &__download {
        width: 27px;
        height: 27px;
        display: block;
        margin-left: auto;
        background: url(data:image/svg+xml;base64,) center center no-repeat;

        @include min-break('1025px') {
            &:hover {
                background: url(data:image/svg+xml;base64,) center center no-repeat;
            }
        }
    }

    &__author {
        font-family: $sharpSansBookFont;
        margin-left: 15px;
        margin-bottom: 0;

        &-img {
            border: 0.843373px solid #323254;
            border-radius: 50%;
        }

        &-wrap {
            display: flex;
            align-items: center;
            margin: 0 0 20px;

            @include min-break('1024px') {
                margin: 0 0 24px;
            }
        }
    }

    &__content {
        height: 100%;
        display: flex;
        flex-direction: column;
        padding: 20px 24px 14px;
    }

    .swiper-slide {
        height: auto !important;
    }
}
