.banner-block {

    .container {
        position: relative;
    }

    .privacy-policy & {
        &__wrap {
            height: 290px;

            @include min-break('1024px') {
                height: 505px;
            }

            &_mobile {
                display: none;
            }
        }

        &__figure {
            top: 0;

            @include min-break('1024px') {
                right: 0;
            }
        }
    }

    .terms-of-service & {
        &__wrap {
            height: 290px;

            @include min-break('1024px') {
                height: 505px;
            }

            &_mobile {
                display: none;
            }
        }

        &__figure {
            bottom: -100px;
            right: -220px;

            @include min-break('1024px') {
                bottom: 55px;
                right: 0;
            }
        }
    }

    .single-resources & {
        &__wrap {
            height: 200px;
            background-position: 100% 100%;
            display: none;

            @include min-break('740px') {
                height: 675px;
                display: block;
            }

            @include max-break('1024px') {
                background-position: 30% 100%;
            }

            &_mobile {
                background-size: 100%;

                @include min-break('740px') {
                    display: none;
                }
            }

        }
    }

    .single-events & {
        &__wrap {
            height: 794px;
            background-position: 100% 100%;
            display: none;
            overflow: hidden;

            @include min-break('1024px') {
                display: block;
                height: 670px;
            }

            &_mobile {
                overflow: hidden;
                position: relative;
                background-position: 100% 0;
                background-size: contain;

                @include min-break('1024px') {
                    display: none;
                }
            }
        }

        &__description {
            @include max-break('1024px') {
                max-width: 500px;
            }
        }

        &__figure {
            position: absolute;
            left: 0;
            top: 0;
            transform: scale(1.2) rotate(0) translateX(0) translateY(0);
            opacity: 1;

            @include max-break('1024px') {
                left: -15%;
            }

            &_mobile {
                position: absolute;
                width: 100%;
                bottom: 0;
                top: auto;

                @include min-break('740px') {
                    bottom: -85%;
                }
            }
        }
    }

    &__figure {
        position: absolute;
        z-index: 1;
        transform: scale(2) rotate(-15deg) translateX(-104px) translateY(304px);
        transition: all 2s ease-in;
        opacity: 0;
    }

    &__back {
        @extend %btn-text;
        position: relative;
        margin: 0;
        padding-left: 30px;

        &:after {
            right: auto;
            left: 0;
            top: 45%;
            transform: translateY(-50%) scale(-1, 1);
        }

        &-wrap {
            align-items: center;
            justify-content: space-between;
            margin: 18px 0 16px;
            display: none;

            @include min-break('1024px') {
                display: flex;
            }
        }

        &-text {
            margin: 0;
        }
    }

    &__wrap {
        position: relative;
        overflow: hidden;
        background-repeat: no-repeat;
        width: 100%;

        &_mobile {
            background-repeat: no-repeat;
        }
    }

    &__content {
        position: relative;
        z-index: 2;

        &.center {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            flex-direction: column;
        }

        &.left {
            max-width: 583px;
            padding: 110px 0 0 60px;

            @include max-break('768px') {
                max-width: 100%;
                padding: 400px 18px 44px;
            }

            @include max-break('430px') {
                padding: 288px 18px 44px;
            }
        }
    }

    &__title {
        color: $primary4;
        margin: 0;
        transform: translateY(100%);
        position: relative;
        z-index: 30;
        transition: all 1.3s ease-in;

        &-wrap {
            overflow: hidden;
            margin: 0 0 30px;

            @include max-break('430px') {
                text-align: center;
                margin: 0 0 20px;
            }
        }
    }

    &__subtitle {
        color: $white;
        margin: 0;
    }

    &__date {
        color: $primary4;
        margin: 0 0 23px;

        @include max-break('430px') {
            text-align: center;
            margin: 0 0 14px;
        }
    }

    &__description {
        color: $primary4;
        margin: 0 0 40px;
        font-family: $sharpSansBookFont;

        @include max-break('430px') {
            text-align: center;
            margin: 0 0 30px;
        }
    }

    &__button {
        position: relative;
        background: $secondary3;
        text-transform: capitalize;
        color: $primary2;
        padding: 5px 21px;

        @media screen and (orientation: portrait) {
            &:active {
                background: $primary1 !important;
                color: $primary4 !important;
            }
        }

        @include max-break('430px') {
            margin: 0 auto;
            display: flex;
            justify-content: center;
            width: 114px;
        }
    }

    &__terms {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 43px 0;

        @include min-break('1200px') {
            margin: 78px 0 44px;
        }

        a {
            pointer-events: none;
            background: $secondary3;
            cursor: initial;
            border-radius: 40px;
            padding: 5px 18px;
            font-family: $sharpSansSemiboldFont;
            font-style: normal;
            font-weight: 800;
            font-size: 12px;
            text-decoration: none;
            line-height: 1.97;
            text-align: center;
            text-transform: capitalize;
            color: $primary2;
            margin-right: 8px;

            @include min-break('1200px') {
                margin-right: 16px;
                font-size: 16px;
            }

            &:last-of-type {
                margin-right: 0;
            }
        }
    }
}
