.resources-banner {
    position: relative;
    height: 680px;
    border: 0 solid #BBB;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: all 0.05s linear;
    color: #FFFCED;

    @include max-break('1024px') {
        height: 620px;
    }

    @include max-break('768px') {
        overflow: hidden;
    }

    &__wrap {
        position: relative;
        height: 100%;
    }

    &__section {
        position: relative;
        width: 100vw;
        height: 100%;
    }

    &__image {
        position: absolute;
        width: 100vw;
        height: 100%;
        left: 0;
        top: 0;
    }

    &__content {
        position: relative;
        padding: 160px 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        z-index: 122;

        @include max-break('1024px') {
            padding: 84px 0 0;
        }
    }

    &__figure {
        position: absolute;
        right: -185px;
        bottom: -230px;
        width: 730px;
        transform: scale(2) rotate(-15deg) translateX(-104px) translateY(304px);
        transition: all 1.3s ease-in;
        opacity: 0;

        @include min-break('768px') {
            bottom: 0;
        }

        @include min-break('1024px') {
            right: 0;
        }
    }

    &__title {
        color: $primary4;
        text-transform: capitalize;
        transform: translateY(100%);
        position: relative;
        z-index: 30;
        transition: all 1.3s ease-in;
        margin: 0;

        &-wrap {
            overflow: hidden;
            margin: 0 0 62px;
        }
    }

    &__form {
        position: relative;
        margin: 0 0 24px;
        display: flex;
        padding: 11px 20px;
        align-items: center;
        width: 100%;
        width: -webkit-fill-available;
        background: $primary4;
        border-radius: 10px;

        @include min-break('1024px') {
            margin: 0 0 35px;
            padding: 7px 20px 6px;
        }

        &-select {
            position: relative;
            white-space: nowrap;
            cursor: pointer;
            display: none;

            @include min-break('1024px') {
                display: block;
            }

            &_mobile {
                position: relative;
                border: 1px solid $primary4;
                border-radius: 10px;
                margin-right: 16px;

                &:last-of-type {
                    margin: 0;
                }
            }
        }
    }

    &__filters-mobile {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include min-break('1024px') {
            display: none;
        }
    }

    &__current {
        position: relative;
        margin: 0;
        padding: 0 23px 0 13px;
        white-space: nowrap;
        line-height: 1.97;

        @include max-break('1023px') {
            color: $primary4;
            padding: 10px 35px 10px 13px;
            font-size: 16px;
            line-height: 1.45;
        }

        &.open {
            &:after {
                transform: translateY(-50%);

                @include max-break('1023px') {
                    transform: translateY(-50%) scale(1, -1);
                }
            }
        }

        &:before {
            position: absolute;
            content: '';
            display: none;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 1.5px;
            height: 27px;
            z-index: 1;
            background: $primary2;

            @include min-break('1024px') {
                display: block;
            }
        }

        &:after {
            position: absolute;
            content: '';
            display: block;
            right: 13px;
            top: 50%;
            transform: translateY(-50%);
            width: 14px;
            height: 8px;
            z-index: 1;
            background: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTQiIGhlaWdodD0iOCIgdmlld0JveD0iMCAwIDE0IDgiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTEuMDAxMDkgMC45OTk5OUw2Ljg1NTQ3IDYuODU0MzdMMTIuNzA5OCAwLjk5OTk4OSIgc3Ryb2tlPSIjRkZGQ0VEIiBzdHJva2Utd2lkdGg9IjEuNSIgc3Ryb2tlLWxpbmVqb2luPSJiZXZlbCIvPjwvc3ZnPg==) center center no-repeat;

            @include min-break('1024px') {
                transform: translateY(-50%) scale(1, -1);
                right: 0;
                background: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTQiIGhlaWdodD0iOCIgdmlld0JveD0iMCAwIDE0IDgiIGZpbGw9Im5vbmUiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTEzLjAwMDkgNi41MDAwMUw3LjE0NjQ4IDAuNjQ1NjNMMS4yOTIxIDYuNTAwMDEiIHN0cm9rZT0iIzMyMzI1NCIgc3Ryb2tlLXdpZHRoPSIxLjUiIHN0cm9rZS1saW5lam9pbj0iYmV2ZWwiLz48L3N2Zz4=) center center no-repeat;
            }
        }

        @include min-break('1025px') {
            &:hover {
                color: $primary1;
            }
        }
    }

    &__lists {
        display: none;
        position: absolute;
        top: calc(100% + 12px);
        white-space: nowrap;
        right: 0;
        padding: 16px 20px;
        background: $primary4;
        border-radius: 10px;
        width: 116px;

        @include min-break('1024px') {
            right: -20px;
        }

        &.open {
            display: block;
        }
    }

    &__list {
        padding: 0 14px 12px 0;
        border-bottom: 1px solid rgba(50, 50, 84, 0.2);
        margin: 0 0 12px;
        white-space: nowrap;
        cursor: pointer;
        color: $black;
        text-align: left;

        &:last-of-type {
            margin: 0;
            padding: 0;
            border: none;
        }

        &.active {
            color: $primary1;
            font-family: $sharpSansSemiboldFont;
        }

        @include min-break('1025px') {
            &:hover {
                color: $primary1;
                font-family: $sharpSansSemiboldFont;
            }
        }
    }

    &__search {
        width: 100%;
        background: transparent;
        border: none;
        outline: none;
        margin: 0;
        font-size: 16px;

        &::placeholder {
            color: $primary1;
            font-family: $sharpSansSemiboldFont;
            line-height: 1.97;
            font-size: 16px;
            text-transform: capitalize;
        }
    }

    &__result {
        overflow-y: scroll;

        &.scroll {
            height: 164px;
            overflow-y: scroll;
            scrollbar-width: 6px;
            scrollbar-color: $primary1 $secondary2;

            @include min-break('1024px') {
                height: 202px;
            }

            &::-webkit-scrollbar-thumb,
            &::-webkit-scrollbar, {
                background-color: $primary1;
                border-radius: 30px;
                width: 6px;
                transform: translateX(-23px);
            }

            &::-webkit-scrollbar-track {
                background-color: $secondary2;
                border-radius: 30px;
            }
        }

        &-wrap {
            position: absolute;
            border-radius: 10px;
            left: 0;
            top: calc(100% + 10px);
            z-index: 999;
            width: -webkit-fill-available;
            background: $primary4;
            padding: 17px 14px 22px 22px;

            @include min-break('1024px') {
                padding: 20px 24px 12px 32px;
                width: 80%;
            }

            &.hide {
                display: none;
            }
        }

        &_title {
            position: relative;
            cursor: pointer;
            margin: 0 0 10px;
            padding: 0 0 10px;

            @include min-break('1024px') {
                margin: 0 0 7px;
                padding: 0 0 7px;
            }

            &:after {
                position: absolute;
                content: '';
                display: block;
                left: 0;
                bottom: 0;
                width: 121px;
                height: 1px;
                z-index: 1;
                background: $primary2;
                opacity: 0.2;
            }

            &:last-of-type {
                margin: 0;
                padding: 0;

                &:after {
                    display: none;
                }
            }

            @include min-break('1025px') {
                &:hover {
                    color: $primary1;
                    font-family: $sharpSansSemiboldFont;
                }
            }

            .highlight {
                color: $primary1;
                font-family: $sharpSansSemiboldFont;
            }
        }
    }

    &__tag {
        padding: 2px 18px;
        background: $primary2;
        color: $primary4;
        margin: 0 17px 0 0;
        border-radius: 40px;
        outline: none;
        border: none;
        cursor: pointer;
        text-transform: capitalize;
        display: none;

        @include min-break('1024px') {
            display: inline-block;
        }

        &:last-of-type {
            margin: 0;
        }

        @include min-break('1025px') {
            &.active,
            &:hover {
                background: $primary4;
                color: $primary2;
            }

            &.loading {
                position: relative;

                &:before {
                    position: absolute;
                    content: '';
                    display: block;
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 1111;
                    background: $primary4;
                    border-radius: 40px;
                }

                &:after {
                    position: absolute;
                    content: '';
                    display: block;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    width: 24px;
                    height: 24px;
                    z-index: 9999;
                    background: url(data:image/svg+xml;base64,) center center no-repeat;
                    background-size: 24px;
                }
            }
        }
    }
}
