.our-lifestyle {
    background-color: $primary4;
    position: relative;
    padding-bottom: 108px;
    z-index: 12;

    @include min-break('1024px') {
        padding-bottom: 0;
    }

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        background: #323254;
        width: 100%;
        height: 133px;
        clip-path: polygon(36% 40%, 100% 0, 100% 100%, 0 100%, 0% 98%);
        display: none;

        @include min-break('1024px') {
            display: block;
        }
    }

    .container {
        padding: 20px 0 0;
        position: relative;
        z-index: 9;
    }

    &__title {
        font-size: 80px;
        line-height: 80px;
        color: $primary1;
        font-family: $larkenFont;
        max-width: 268px;
        text-align: center;
        position: relative;
        z-index: 1;

        @include min-break('1024px') {
            font-size: 200px;
            line-height: 188px;
            max-width: 830px;
        }
    }
    &__wrapper {
        overflow-x: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        min-height: calc(100vw * 2.16);
        padding-bottom: calc(100vw * .25);

        @include min-break('768px') {
            padding-bottom: calc(100vw * .12);
            min-height: calc(100vw * .7);
        }
        @include min-break('1024px') {
            padding-bottom: 0;
            min-height: calc(100vw * .8);
        }
    }
    &__image {
        position: absolute;
        animation: k_image 3s cubic-bezier(0.5, 0, 0.5, 1) infinite alternate;

        img {
            display: block;
            width: 100%;
            height: auto;
        }

        &--image_1 {
            top: 26%;
            left: 70%;
            width: 51%;

            @include min-break('768px') {
                top: 18%;
                left: 7%;
                width: 27%;
            }
        }

        &--image_2 {
            display: none;
            animation-delay: -.5s;

            @include min-break('768px') {
                top: 7%;
                left: 40%;
                width: 13%;
                display: block;
            }
            @include min-break('1024px') {
                top: 17%;
            }
        }

        &--image_3 {
            top: 14%;
            left: 17px;
            width: 38%;
            animation-delay: -1.5s;

            @include min-break('768px') {
                top: 11%;
                left: auto;
                right: 16%;
                width: 21%;
            }
        }
        &--image_4 {
            top: 54%;
            left: -5%;
            width: 42%;
            animation-delay: -1s;

            @include min-break('768px') {
                top: 43%;
                left: 11%;
                width: 20%;
            }
            @include min-break('1024px') {
                top: 50%;
            }
        }
        &--image_5 {
            left: 11%;
            bottom: -2%;
            width: 52%;
            animation-delay: -1s;

            @include min-break('768px') {
                top: 56%;
                bottom: auto;
                left: 38%;
                width: 13%;
            }
            @include min-break('1024px') {
                top: 60%;
            }
        }
        &--image_6 {
            top: 0;
            right: 7%;
            width: 43%;
            animation-delay: -2s;

            @include min-break('768px') {
                top: 50%;
                right: 23%;
                width: 20%;
            }
            @include min-break('1024px') {
                top: 60%;
            }
        }
        &--image_7 {
            top: 57%;
            left: 50%;
            width: 68%;
            animation-delay: -1s;

            @include min-break('768px') {
                bottom: 6%;
                left: 17%;
                width: 32%;
                top: auto;
            }
            @include min-break('1024px') {
                bottom: 4%;
            }
        }
        &--image_8 {
            display: none;

            @include min-break('768px') {
                top: 36%;
                right: 7%;
                width: 12%;
                display: block;
            }
        }
    }

}

@keyframes k_image {
    from {
        transform: translate3d(0, -20%, 0px);
    }

    to {
        transform: translate3d(0, 20%, 0px);
    }
}
