.speaking-bubble {
    position: absolute;
    width: 429px;
    display: flex;
    justify-content: flex-end;
    bottom: -130px;
    right: 16px;
    z-index: 120;
    transition: opacity 0.3s ease-in-out;
    opacity: 1;

    @include max-break('768px') {
        bottom: 0;
    }

    &.hide {
        opacity: 0;
    }

    @include min-break('1024px') {
        bottom: -44px;
        right: 109px;
    }

    &__image {
        position: relative;
        z-index: 2;
        display: none;

        &.show {
            display: flex;
            animation: image 1s;
        }
    }

    &__text {
        right: 38px;
        top: -80px;
        position: absolute;
        justify-content: center;
        align-items: center;
        z-index: 3;
        width: 309px;
        height: 117px;
        display: none;

        &.show {
            display: flex;
            animation: image2 1s;
        }

        @include min-break('1024px') {
            top: -50px;
            left: 0;
        }

        &:after {
            position: absolute;
            content: '';
            display: block;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
        }
    }

    &__animate-text {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        z-index: 3;
        max-width: 240px;

        @include max-break('414px') {
            margin-top: -20px;
            max-height: 45px;
        }


        span {
            color: $primary4;
            font-size: 14px;
            margin: 0 4px 0 0;
            z-index: 2;
            animation: letter 1s;
            display: inline-block;
        }
    }
}

@keyframes letter {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 1;
        visibility: visible;
    }
}

@keyframes image {
    0% {
        visibility: hidden;
        opacity: 0;
        transform: translateX(10%) translateY(10%);
    }
    100% {
        opacity: 1;
        visibility: visible;
        transform: translateX(0) translateY(0);
    }
}

@keyframes image2 {
    0% {
        visibility: hidden;
        opacity: 0;
        transform: translateX(5%) translateY(5%);
    }
    100% {
        opacity: 1;
        visibility: visible;
        transform: translateX(0) translateY(0);
    }
}
