.our-perks {
    background: $primary4;
    z-index: 10;
    position: relative;
    padding: 0 0 130px;

    @include min-break('1024px') {
        padding: 93px 0 50px;
    }

    &__title {
        text-align: center;
        padding: 33px 0 31px;

        h3 {
            font-size: 21px;
            line-height: 28px;
            color: $primary2;
            font-family: $sharpSansSemiboldFont;

            @include min-break('1024px') {
                font-size: 45px;
                line-height: 42px;
            }
        }

    }
    &__blocks-container {
        margin: 0 42px;

        @include min-break('580px') {
            margin: 0;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 44px 20px;
        }
        @include min-break('1024px') {
            grid-template-columns: repeat(4, 1fr);
        }
    }
    &__box {
        text-align: center;
        padding: 0 19px 20px;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        @include min-break('1024px') {
            padding: 0 27px 40px;
        }
        @include min-break('1280px') {
            padding: 0 50px 40px;
        }

        &:after {
            content: '';
            background-color: $secondary3;
            width: 100%;
            position: absolute;
            z-index: -1;
            left: 0;
            bottom: 0;
            height: 119px;

            @include min-break('1024px') {
                height: 159px;
            }
        }

        &-title {
            font-size: 20px;
            line-height: 25px;
            font-family: $sharpSansBookFont;
            color: $primary2;
            margin-top: 20px;
            min-height: 50px;
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;

            @include min-break('1024px') {
                font-size: 25px;
                line-height: 30px;
                margin-top: 26px;
            }
        }

        &:not(:first-child) {
            margin-top: 24px;

            @include min-break('580px') {
                margin-top: 0;
            }
        }
    }
    &__icon-block {
        flex: 1;

        img {
            transition: scale 0.5s;
            @include min-break('1024px') {
                scale: 1.1;
            }
        }
    }
}
