html {
    position: relative;
    min-height: 100%;

    &.popup-opened {
        overflow: hidden;

        @media screen and (max-width: 768px) {
            height: 100vh;
        }
    }
}


/* ********************** MODAL WINDOW ********************************/

.my_overlay {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $primary2;
    opacity: 0.9;
    cursor: pointer;
    display: block;
}

.popup {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 5000;
    display: none;
    overflow-x: hidden;
    overflow-y: scroll;

    &#book-a-demo .popup-wrapper-inner {
        max-width: 1030px;
    }

    &#popup-lock-post .popup-wrapper-inner {
        max-width: 1163px;
    }

    &#cookies .popup-wrapper-inner {
        max-width: 840px;
        max-height: 600px;
        overflow: scroll;
    }

    .popup-wrapper-inner {
        position: relative;
        z-index: 2;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @include max-break('1024px') {
            max-width: 100vw;
            padding: 0 16px;
        }

        @include max-break('414px') {
            max-width: 100vw;
            padding: 42px 16px;
            left: 0;
            top: 0;
            transform: none;
        }
    }

    button.popup-close {
        background: url(data:image/svg+xml;base64,) center center no-repeat;
        text-indent: -9999px;
        background-size: 32px auto;
        border: none;
        width: 34px;
        height: 34px;
        position: absolute;
        top: 30px;
        right: 30px;
        padding: 0;
        z-index: 10;
        cursor: pointer;

        @include max-break('414px') {
            top: 18px;
            right: 18px;
        }
    }

    .hbspt-form {
        margin: 54px 0 0;
        max-width: 340px;
        width: 100%;

        @include max-break('1024px') {
            margin: 0 auto;
            max-width: 280px;
        }

        @include max-break('414px') {
            max-width: 226px;
        }
    }

    .hs-error-msg {
        font-size: 11px;
        font-family: $sharpSansBookFont;
        color: $wrong;
    }

    .hs_error_rollup {
        display: none;
    }

    .hs-form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .input {
        margin: 0 !important;
    }

    fieldset {
        width: 100% !important;
    }

    .hs-form-field {
        position: relative;
        margin: 0 0 30px;
        display: block;
        width: 100%;

        @include max-break('768px') {
            margin: 0 0 20px;
        }

        & > label {
            position: absolute;
            top: 0;
            left: 20px;
            z-index: 2;
            cursor: text;
            transform-origin: 0 100%;
            transform: translateY(10px);
            transition: 0.2s ease-out;

            span {
                font-family: $sharpSansBookFont;
                font-weight: 800;
                font-size: 16px;
                line-height: 1.38;
                color: $primary1;

                @include max-break('414px') {
                    font-size: 12px;
                }
            }

            &.focus {
                transform: translateY(-20px);
                color: $primary1;
                left: 0;
                font-size: 12px;
                line-height: 1.97;
            }
        }

        &__remove {
            position: absolute;
            content: '';
            cursor: pointer;
            display: none;
            right: 13px;
            top: 50%;
            transform: translateY(-50%);
            width: 12px;
            height: 12px;
            z-index: 20;
            background: url(data:image/svg+xml;base64,) center center no-repeat;
        }

        &.error {
            .input-field__remove {
                display: block;
            }
        }
    }

    .hs-input {
        background: transparent;
        font-family: $sharpSansBookFont;
        border: none;
        outline: none;
        width: -webkit-fill-available !important;
        padding: 10px 20px;
        border-radius: 10px;
        background: $white;
        font-weight: 800;
        font-size: 16px;
        line-height: 1.38;
        color: $primary2;
        position: relative;
        z-index: 1;

        @include max-break('414px') {
            font-size: 12px;
            padding: 10px 15px;
        }

        &::placeholder {
            color: transparent;
        }

        &.error {
            color: $wrong;
        }
    }

    .hs-submit {
        margin: 0 auto;
    }

    .actions {
        padding: 5px 21px;
        margin: 0 auto;
    }

    .hs-button {
        background: transparent;
        outline: none;
        border: none;
        font: inherit;
        padding: 0;
        margin: 0;
        color: inherit;
    }
}

.popup-lock-post {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background: $primary1;
    padding: 105px 75px 70px 60px;

    @include max-break('1024px') {
        padding: 105px 30px 70px 60px;
    }

    @include max-break('768px') {
        padding: 50px 24px 40px;
    }

    @include max-break('760px') {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &.sent {
        padding: 105px 75px 40px 60px;

        @include max-break('768px') {
            padding: 50px 24px 40px;
        }

        .popup-lock-post {
            &__thanks {
                display: flex;
            }

            &__section-second {
                padding: 0;
            }
        }

        .hbspt-form {
            display: none;
        }

        .popup-title {
            display: none;
        }
    }

    &__section-first {
        position: relative;
        z-index: 2;
        max-width: 500px;

        @include max-break('1200px') {
            max-width: 350px;
        }

        @include max-break('768px') {
            max-width: 300px;
        }
    }

    &__section-second {
        position: relative;
        z-index: 2;
        padding: 30px 0 0;
        max-width: 340px;
        width: 100%;

        @include max-break('414px') {
            padding: 70px 0 0;
        }

        .subtitleMBold {
            text-align: center;
        }
    }

    &__thanks {
        display: none;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h4, p {
            text-align: center;
        }

        h4 {
            margin: 30px 0 0;

            @include max-break('414px') {
                margin: 12px 0 0;
                font-size: 40px;
            }
        }

        .bodyLBold {
            @include max-break('414px') {
                font-size: 16px;
                margin: 0 0 12px;
            }
        }

        &_desc {
            color: $primary1;
            margin: 0;

            @include max-break('414px') {
                max-width: 230px;
            }
        }
    }

    &__caption {
        color: $primary4;
        margin: 0 0 36px;
        max-width: 335px;

        @include max-break('414px') {
            max-width: 235px;
            margin: 0 auto 20px;
            text-align: center;
            font-size: 30px;
        }
    }

    &__description {
        color: $primary4;
        margin: 0 0 36px;

        @include max-break('414px') {
            margin: 0 0 20px;
        }
    }

    &__list {
        color: $primary4;
        margin: 0 0 15px;
        padding-left: 34px;
        position: relative;

        &:after {
            position: absolute;
            content: '';
            display: block;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 13px;
            height: 11px;
            z-index: 1;
            background: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTMiIGhlaWdodD0iMTEiIHZpZXdCb3g9IjAgMCAxMyAxMSIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48cGF0aCBkPSJNMCA1TDQgMEgxMEwxMi41IDVMMTAgOUw0IDExTDAgNVoiIGZpbGw9IiNFQzlGRkYiLz48L3N2Zz4=) center center no-repeat;
        }

        &:last-of-type {
            margin: 0;
        }
    }

    &__figure {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;

        @include max-break('1024px') {
            bottom: 0;
            right: -10%;
            top: auto;
        }

        @include max-break('414px') {
            display: none;
        }

        &_mobile {
            position: absolute;
            width: 100%;
            bottom: 0;
            left: 0;
            display: none;

            @include max-break('414px') {
                display: block;
            }
        }
    }
}

.book-a-demo {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    background: $primary1;
    padding: 147px 46px 87px 72px;

    @include max-break('768px') {
        padding: 77px 58px 65px;
    }

    @include max-break('414px') {
        flex-direction: column;
        justify-content: center;
    }

    &.sent {
        height: 306px;

        @include max-break('414px') {
            height: 482px;
            justify-content: flex-start;
        }

        .book-a-demo {

            &__thanks {
                display: flex;
            }

            &__section-second {
                position: initial;
                z-index: 10;
            }
        }

        .hbspt-form {
            display: none;
        }
    }

    &__section-first {
        position: relative;
        z-index: 5;
        max-width: 400px;
        padding: 5px 0 0;

        @include max-break('768px') {
            max-width: 250px;
            transform: translateX(-25px);
        }
    }

    &__caption {
        color: $primary4;
        margin: 0 0 24px;

        @include max-break('414px') {
            max-width: 235px;
            margin: 0 0 10px;
        }
    }

    &__description {
        color: $primary4;
        max-width: 368px;
        margin: 0;

        @include max-break('768px') {
            max-width: 225px;
        }
    }

    &__section-second {
        position: relative;
        z-index: 3;

        @include max-break('414px') {
            margin: 76px 0 0;
        }

        .subtitleMBold {
            text-align: center;
        }
    }

    &__thanks {
        display: none;
        position: absolute;
        right: 44px;
        bottom: 0;
        z-index: 10;

        @include max-break('768px') {
            transform: scale(0.8);
            bottom: -44px;
        }

        @include max-break('414px') {
            transform: none;
            bottom: -4px;
            left: 0;
        }

        &_svg {
            @include max-break('414px') {
                display: none;
            }
        }

        &_svg-mobile {
            display: none;

            @include max-break('414px') {
                display: block;
            }
        }
    }

    &__figure {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;

        @include min-break('1025px') {
            height: 100%;
        }

        @include max-break('768px') {
            top: -28%;
        }

        @include max-break('414px') {
            display: none;
        }

        &_mobile {
            display: none;
            position: absolute;
            left: -20px;
            z-index: 2;
            border-bottom: 14px solid #dbd8ff;
            bottom: -20px;
            width: 100vw;

            @include max-break('414px') {
                display: block;
            }
        }
    }

    &__cloud {
        position: absolute;
        z-index: 1;

        @include max-break('768px') {
            display: none;
        }

        &_mobile {
            display: none;
            position: absolute;
            z-index: 1;

            @include max-break('768px') {
                display: block;
            }
        }

        &-first {
            top: 5px;
            left: 0;

            &_mobile {
                top: 18px;
                left: 123px;
            }
        }

        &-second {
            top: 0;
            right: 278px;

            &_mobile {
                top: 18px;
                left: 0;
            }
        }
    }

    &__footprints {
        position: absolute;
        bottom: -85px;
        right: 408px;
        z-index: 3;

        @include max-break('1024px') {
            right: 358px;
        }

        @include max-break('414px') {
            display: none;
        }

        &_mobile {
            display: none;
            position: absolute;
            z-index: 3;
            bottom: 17px;
            left: 17px;

            @include max-break('414px') {
                display: block;
            }
        }
    }

    &__arrow-sign {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 4;

        @include max-break('768px') {
            display: none;
        }

        &_mobile {
            display: none;
            position: absolute;
            z-index: 4;
            top: 0;
            left: 0;

            @include max-break('768px') {
                display: block;
            }
        }

        &_foot {
            position: absolute;
            bottom: 0;
            left: 244px;
            z-index: 1;

            @include max-break('768px') {
                display: none;
            }

            &_mobile {
                display: none;
                position: absolute;
                z-index: 1;
                top: 256px;
                left: 45px;

                @include max-break('768px') {
                    display: block;
                }
            }
        }
    }

    button.popup-close {
        top: 11px;
        right: 14px;

        @include max-break('414px') {
            top: 18px;
            right: 18px;
        }
    }

    .hs_firstname {
        float: left !important;
    }

    .hs_lastname {
        float: right !important;
    }


    .hs_firstname,
    .hs_lastname {
        width: calc(50% - 10px) !important;

        @include max-break('414px') {
            width: calc(50% - 5px) !important;
        }
    }
}

.cookies-popup {
    position: relative;
    overflow: hidden;
    padding: 42px 70px 48px 58px;
    background: $primary2;

    &__title {
        color: $white;
        text-align: center;
    }

    &__block {

        &_caption {
            color: $white;
        }

        &_text {
            color: $white;
        }
    }
}
