.banner-block-video {
    max-height: 710px;
    .container {
        position: relative;
    }

    &__figure {
        position: absolute;
        z-index: 1;
        transform: scale(2) rotate(-15deg) translateX(-104px) translateY(304px);
        transition: all 1.3s ease-in;
        opacity: 0;
        top: -70px;
        width: 270px;

        &:not(.js-parallax-figure) {
            transform: none;
            opacity: 1;
        }

        &.desktop {
            display: none;
        }

        @include min-break('768px') {
            left: 0;
            width: auto;

            &.mobile {
                display: none;
            }

            &.desktop {
                display: block;
            }
        }
    }

    &__wrap {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 320px;
        z-index: 2;

        @include min-break('768px') {
            height: 540px;
        }

        @include min-break('1024px') {
            height: 100vh;
        }
    }

    &__content {
        position: relative;
        z-index: 2;

        &.center {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            flex-direction: column;
        }
    }

    &__title {
        color: $primary4;
        margin: 0;
        transform: translateY(100%);
        position: relative;
        z-index: 30;
        transition: all 1.3s ease-in;
        max-width: 230px;
        font-size: 40px;
        line-height: 42px;
        text-align: center;

        @include min-break('1024px') {
            max-width: 710px;
            font-size: 140px;
            line-height: 123px;
        }

        &-wrap {
            overflow: hidden;
            margin: 0 0 30px;

            @include max-break('414px') {
                text-align: center;
                margin: 0 0 20px;
            }
        }
    }
    &__video {
        min-width: 100%;
        min-height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
    }
}
