.cky-overlay {
    background: $primary2 !important;
    opacity: 0.7 !important;
}

.cky-title {
    display: none !important;
}

.cky-notice-des {
    padding: 0 !important;

    p {
        @include max-break('430px') {
            font-size: 13px !important;
            text-align: center !important;
            font-family: $sharpSansBookFont !important;
            line-height: 1.38 !important;
        }
    }
}

.cky-notice-btn-wrapper {
    padding: 0 !important;

    @include max-break('430px') {
        flex-direction: row !important;
        flex-wrap: wrap;
        margin-top: 25px !important;
        justify-content: center !important;
    }
}

.cky-consent-bar {
    box-shadow: none !important;
    border: none !important;
    padding: 0 25px !important;
    margin: 0 auto;

    @include max-break('430px') {
        padding: 0 34px !important;
    }

    @include min-break('1360px') {
        max-width: 1240px;
        padding: 0 !important;
    }
}

.cky-consent-container {
    background: #323254;
    padding: 45px 0 40px;

    @include max-break('430px') {
        padding: 45px 0;
    }
}


.cky-consent-container,
.cky-modal {
    .cky-btn {
        font-family: $sharpSansSemiboldFont;
        width: fit-content !important;
        background: #474774 !important;
        margin: 0 14px 0 0 !important;
        border: none !important;
        color: $primary4 !important;
        flex: none !important;
        font-size: 16px !important;

        &:hover {
            opacity: 1 !important;
        }

        @media screen and (orientation: portrait) {
            &:active {
                background: $primary1 !important;
                color: $primary4 !important;
            }
        }
    }

    .cky-btn-accept {
        background: $primary1 !important;
        margin: 0 !important;
        order: 3 !important;

        @include max-break('430px') {
            margin: 18px 0 0 0 !important;
            width: 97px !important;
        }
        @media screen and (orientation: portrait) {
            &:active {
                background: $primary3 !important;
                color: $primary4 !important;
            }
        }
    }

    .cky-btn-customize,
    .cky-btn-preferences {
        background: $secondary3 !important;
        color: $primary2 !important;
        order: 1 !important;

        @include max-break('430px') {
            width: 100% !important;
            max-width: 200px !important;
            margin: 0 50px !important;
        }
    }

    .cky-btn-customize {
        @include max-break('430px') {
            background: none !important;
            color: $white !important;
            text-decoration: underline;
        }
    }

    .cky-btn-reject {
        order: 2 !important;

        @include max-break('430px') {
            width: 93px !important;
            margin: 18px 10px 0 0 !important;
        }
    }
}

.cky-modal-open {
    .cky-btn-reject {

        @include max-break('430px') {
            width: 93px !important;
            margin: 0 10px 18px 0 !important;
            order: 1 !important;
        }
    }

    .cky-btn-accept {
        @include max-break('430px') {
            order: 2 !important;
            margin: 0 0 18px 0 !important;
        }
    }

    .cky-btn-preferences {
        @include max-break('430px') {
            order: 3 !important;
        }
    }
}


.cky-modal {

    @include max-break('430px') {
        padding: 0 16px !important;
        background: transparent !important;
        border-radius: 0 !important;
        box-shadow: none !important;
    }

    .cky-preference {
        &-center {
            border: none !important;
            border-radius: 0 !important;
            position: relative;
            padding: 40px 0;
            max-height: 520px;
            max-width: 840px;
            width: 840px !important;
        }

        &-header {
            border: none !important;
            padding: 0 !important;
            margin: 0 0 24px;
            justify-content: center !important;

            @include max-break('430px') {
                margin: 0 0 36px;
            }
        }

        &-title {
            font-family: $sharpSansSemiboldFont;
            font-weight: 800 !important;
            font-size: 16px !important;
            line-height: 1.97 !important;
            text-align: center;
            text-transform: capitalize;
        }

        &-body-wrapper {
            padding: 0 70px 160px 56px !important;

            @include max-break('430px') {
                padding: 0 28px 130px !important;
            }
        }

        &-content-wrapper {
            padding: 0 0 12px !important;
            margin: 0 0 12px !important;
            border-bottom: 1px solid rgba(215, 247, 255, 0.3) !important;

            @include max-break('430px') {
                padding: 0 0 22px !important;
                margin: 0 0 20px !important;
            }

            .cky-show-desc-btn {
                color: $secondary2 !important;
                font-family: $sharpSansBookFont;
            }
        }
    }

    .cky-prefrence-btn-wrapper {
        padding: 0 !important;
        border: none !important;
        flex-direction: row !important;
    }

    .cky-footer-shadow {
        background: linear-gradient(0deg, #323254 30.1%, rgba(22, 33, 61, 0) 100%) !important;
        height: 202px !important;
        bottom: -40px !important;

        @include max-break('430px') {
            height: 256px !important;
        }
    }

    .cky-accordion {
        position: relative;
        padding: 0 0 16px !important;
        margin: 0 0 16px !important;
        border-bottom: 1px solid rgba(215, 247, 255, 0.3) !important;

        @include max-break('430px') {
            padding: 0 0 22px !important;
            margin: 0 0 20px !important;
        }

        &:last-of-type {
            padding: 0 !important;
            margin: 0 !important;
            border: none !important;
        }

        &-header {
            margin: 0 !important;

            &-des {
                margin: 0 !important;
            }
        }

        &-btn {
            font-family: $sharpSansSemiboldFont !important;
            font-weight: 800 !important;
            line-height: 1.38 !important;

            @include max-break('430px') {
                font-size: 13px !important;
                margin: 0 0 8px !important;
            }
        }

        &-chevron {
            display: none !important;
        }

        &-item {
            margin: 0 !important;
        }
    }

    .cky-btn-close {
        position: absolute;
        top: 12px;
        right: 12px;
        width: 35px !important;
        height: 35px !important;
        background: url('data:image/svg+xml;base64,') center center no-repeat !important;

        @include max-break('430px') {
            top: 20px;
            right: 20px;
            width: 18px !important;
            height: 18px !important;
            background-size: 28px !important;
        }

        img {
            display: none !important;
        }
    }

    .cky-always-active {
        color: $secondary1 !important;
        font-family: $sharpSansBookFont;
        font-style: normal;
        font-weight: 800 !important;
        font-size: 14px !important;
        line-height: 1.38 !important;
        text-align: center;
        max-width: 50px;
        position: absolute;
        max-width: 50px;
        text-align: center;
        top: 50%;
        right: 0;
        transform: translateY(-50%);

        @include max-break('430px') {
            max-width: 100%;
            position: static;
            transform: none;
        }
    }

    .cky-footer-wrapper {
        position: absolute;
        bottom: 40px;
        width: 100%;

        div:last-of-type {
            display: none !important;
        }
    }

    p {
        max-width: 604px;
        font-family: $sharpSansBookFont !important;
        margin: 0 !important;
        line-height: 1.26;

        @include max-break('768px') {
            max-width: 500px !important;
        }

        @include max-break('430px') {
            font-size: 13px !important;
        }
    }
}
