.archive-posts {
    transition: all 0s ease-in;
    -webkit-transition: all 0s ease-in;
    overflow: hidden;
    position: absolute;
    top: 0;
    width: 100%;
    margin: 670px 0 0;

    @include max-break('1024px') {
        margin: 590px 0 0;
    }

    .cutter {
        height: 165px;
    }

    &__content_wrap {
        background: $primary4;
        height: max-content;
        position: relative;
        padding: 0 0 80px;

        @include min-break('1024px') {
            padding: 0 0 120px;
        }
    }

    &__load-more {

        &-wrap {
            margin: 50px auto 0;
            width: 101px;
            height: 42px;
            display: flex;
            align-items: center;
            justify-content: center;

            &.hide {
                display: none;
            }
        }
    }

    .most-popular {
        &__items {
            display: grid;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
            grid-column-gap: 0;
            grid-row-gap: 48px;

            @include min-break('768px') {
                grid-template-columns: repeat(2, 1fr);
                grid-column-gap: 33px;
                grid-row-gap: 50px;
            }

            @include min-break('1024px') {
                grid-template-columns: repeat(3, 1fr);
            }
        }

        &__item {

            &:first-of-type {

                @include min-break('768px') {
                    display: flex;
                    flex-direction: row;
                    width: 100%;
                    grid-area: 1 / 1 / 2 / 3;
                    padding: 20px 0;
                }

                @include min-break('1024px') {
                    grid-area: 1 / 1 / 2 / 4;
                }

                .most-popular {
                    &__image {
                        max-width: 100%;
                        min-height: 322px;

                        @include min-break('768px') {

                            &-wrap {
                                padding: 0 0 0 20px;
                            }
                        }

                        @include min-break('1024px') {
                            max-width: 410px;
                            min-height: 420px;
                        }
                    }

                    &__title {
                        font-size: 21px;

                        @include min-break('1024px') {
                            font-size: 25px;
                        }
                    }

                    &__excerpt {
                        @include min-break('768px') {
                            display: block;
                        }
                    }

                    &__content {
                        height: auto;

                        @include min-break('1024px') {
                            padding: 29px 45px;
                        }
                    }

                    &__learn-more {
                        @include max-break('414px') {
                            font-size: 13px;

                            &:after {
                                background-size: 16px;
                                height: 9px;
                                width: 16px;
                                right: -24px;
                            }
                        }
                    }

                    &__author {
                        @include min-break('1024px') {
                            font-size: 16px;
                            font-family: $sharpSansSemiboldFont;

                            &-img {
                                width: 70px;
                                height: 70px;
                            }
                        }
                    }
                }
            }

        }

        &__image {
            max-height: 178px;
            min-height: 178px;

            &-wrap {
                position: relative;
                padding: 14px 14px 0;
            }
        }
    }

}
