.author-block {
    padding: 41px 0 45px;

    @include min-break('1024px') {
        padding: 70px 0 40px;
    }

    .single-events & {
        padding: 22px 0 36px;

        @include min-break('1024px') {
            padding: 26px 0 48px;
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;

        @include min-break('768px') {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__image {

        @include max-break('914px') {
            width: 82px;
            height: 82px;
        }

        &-wrap {
            display: flex;
            align-items: center;
            flex-direction: column;
            margin: 0 0 45px;

            @include min-break('768px') {
                flex-direction: row;
                margin: 0;
            }

            .single-events & {
                display: none;
            }
        }
    }

    &__name {
        margin: 12px 0;
        font-size: 20px;

        @include min-break('768px') {
            margin: 0 0 8px;
        }

        .single-events & {
            display: none;
        }

        &-wrap {
            @include min-break('768px') {
                margin-left: 38px;
            }
        }
    }

    &__date {
        margin: 0;
        font-size: 20px;
        text-align: center;

        @include min-break('768px') {
            text-align: left;
        }

        .single-events & {
            display: none;
        }
    }

    &__social {
        cursor: pointer;
        margin-right: 4px;
        display: inline-block;

        &:last-of-type {
            margin-right: 0;
        }

        &:hover path:not(:last-child) {
            fill: $primary2;
        }

        &-wrap {
            display: flex;
            align-items: flex-end;

            @include min-break('768px') {
                margin-left: auto;
            }
        }
    }
}
